import React from 'react'
import LandingBaseLayout from '../LandingBaseLayout'
import { API_LIST } from './ApiList'

const Developer = () => {

    const ApiRender = API_LIST.map(result => {
        return <div class="col-lg-6">
            <div class="card">
                <div class="card-body">
                    <div class="d-flex text-align-start">
                        {/* <img class="me-4 align-self-center flex-shrink-0" src="./assets/images/brands/slack.png" alt="" height="60" /> */}
                        <div class="flex-grow-1">
                            <h5 class="mt-0">{result.title}</h5>
                            <p class="mb-0">
                                {result.desc}
                            </p>
                        </div>

                    </div>
                    <div class="text-right">
                        {!result.comingsoon ? <a
                            href={result.link}
                            target="_blank"
                            className="btn btn-sm btn-outline-primary mb-2"
                        >
                            View Docs
                        </a> : <button

                            disabled
                            className="btn btn-sm btn-disabled btn-outline-primary mb-2"
                        >
                            Coming Soon
                        </button>}
                    </div>

                </div>
            </div>
        </div>
    })
    return (
        <LandingBaseLayout>

            <section className="hero-4 pb-5 pt-7 py-sm-7">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-lg-6 col-md-6">
                            <h1 className="hero-title mt-0">
                                Seamless
                                <span className="highlight highlight-warning d-inline-block">
                                    Web3 Adoption {" "}
                                </span>
                                &nbsp;and User Onboaring experience
                            </h1>
                            <p className="fs-16 text-muted pt-3 w-75">
                                Move away from complex web3 integration and plugin our PayTez for 1 click Integration. Explore our developer document now !.
                            </p>
                            <div className="pt-4 pb-md-5 mb-md-4">
                                <a
                                    href="#apislist"
                                    className="btn btn-secondary mb-2"
                                    data-toggle="smooth-scroll"
                                >
                                    View Docs
                                    <i className="icon-xxs ms-2" data-feather="arrow-right" />
                                </a>
                            </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                            <div className="img-container text-end ps-lg-5" data-aos="zoom-in">
                                <div className="row align-items-center mt-md-0 mt-4">
                                    <div className="col-6">
                                        <div className="card shadow-lg">
                                            <div className="card-body p-1">
                                                <img
                                                    src="./assets/images/feature-invoice-1.png"
                                                    className="img-fluid"
                                                    alt=""
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-6">
                                        <div className="row">
                                            <div className="col">
                                                <div className="card shadow-lg">
                                                    <div className="card-body p-1">
                                                        <img
                                                            src="./assets/images/developerdocs-thumb-2.png"
                                                            className="img-fluid"
                                                            alt=""
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col">
                                                <div className="card shadow-lg">
                                                    <div className="card-body p-1 mb-0">
                                                        <img
                                                            src="./assets/images/developerdocs-thumb-1.png"
                                                            className="img-fluid"
                                                            alt=""
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </section>

            <section class="my-5 py-6 bg-gradient2 position-relative" id="apislist">
                <div class="container" data-aos="fade-up" data-aos-duration="1500">
                    <div class="row">
                        <div class="col text-center">
                            <span class="badge rounded-pill badge-soft-primary px-2 py-1">Integrations</span>
                            <h1 class="display-5 fw-medium">Empowering Seamless Payments</h1>
                            <p class="text-muted mx-auto">
                                Integration Titles for Various Platforms with  <span class="text-primary fw-bold">PayTez</span>.
                            </p>
                        </div>
                    </div>

                    <div class="row mt-5">
                        {ApiRender}
                    </div>

                </div>
            </section>

        </LandingBaseLayout>
    )
}

export default Developer
